@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}
.containerPage{
  margin: 0;
  padding: 0;
}
.navbar {
    background-color: #000000;
    height: 100vh;
    width: 40px;
    display: flex;
    justify-content: center;
    position: fixed;
    float: left;
    z-index:4;
  }
  
  .menu-arrow {
    font-size: 30px;
    background: none;
  }
  
  .nav-menu {
    background-color: #000000;
    width: 220px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 4;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
    z-index: 4;
  }
  
  .nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }
  
  .nav-text a:hover {
    background-color: #2F3146;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color:#000000;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: 10px;
  }
  
  span {
    margin-left: 16px;
  }
.home,
.portafolio {
  background-color: #1a1a2e;
  display: flex;
  text-align: center;
  height:auto;
  margin-left:35px;
  justify-content: center;
  padding-bottom:10%;
}

.aboutMe{
  background-color: #1a1a2e;
  display: flex;
  text-align: center;
  height:100vh;
  margin-left:35px;
  justify-content: center;
  padding-bottom:10%;
}
.leftBanner{
  display: grid;
  text-align: center;
  height:100vh;
  width:60vw;
  justify-content: center;
}
.banner{
    font-size: 60px;
    color: #99CAFF;
    font-weight: bolder;
}
.title{
    margin-top: -10px;
    font-size: 2.5rem;
    color: #BBEEF1;
    font-style: italic;
}
.subtitle{
    font-size: 1.4rem;
    font-weight: bold;
    text-align: center;
    color: #738AAB;
}
.paragraph{
    font-size: 1.4rem;
    text-align: center;
    color: #d5dce6;
}
.content{
    margin-left: auto;
    margin-right: auto;
    width: 10vw;
}

.barFrame{
    background-color: grey;
    justify-content: left;
    margin-left: 30px;
    padding: 0px;
    width: 90%;
    border-radius:25px;
}
.barValue{
    background-color: #D5DCE6; 
    border-radius:25px;
}
.center{
    text-align: center;
    align-items: center;
}
.aling-Left{
    text-align: left;
    align-items: left;
}
.footer{
    margin-bottom:100px;
}
.content{position:relative;height:31vw;width:100%;margin-top:-40px}.content__background,.content__background__shadow,.content__background__image,.content__background__video,.content__area{position:absolute;top:0;bottom:0}.content__background{left:0;right:0}.content__background__shadow{left:0;background:#000;width:30%;z-index:2}.content__background__shadow:before{content:'';position:absolute;z-index:10;background-image:linear-gradient(to right, #000, transparent);top:0;bottom:0;left:100%;width:100%}.content__background__image{right:0;width:70%;height:100%;background-position:center 10%;background-size:cover;z-index:1}.content__background__video{left:19%;width:auto;height:100%;background-position:center 10%;background-size:cover;z-index:1}.content__area{left:0;right:0;height:100%;z-index:3}.content__area__container{color:wheat;height:100%}.content__title{font-size:5vw;color:#fff;font-weight:700}.content__description{font-size:2vw;color:#999;margin-top:5px;max-width:30vw}.content__close{color:#fff;width:40px;height:40px;background:transparent;outline:none;border:none;position:absolute;top:30px;right:30px}

.slide-button{position:absolute;top:0;bottom:0;width:55px;background:rgba(0,0,0,0.5);border:0;outline:0;padding:0;margin:40px 0;z-index:4}.slide-button span{width:25px;color:#fff;display:block;margin:0 auto}.slide-button--next{right:0}.slide-button--next span{transform:rotateZ(-90deg)}.slide-button--prev{left:0}.slide-button--prev span{transform:rotateZ(90deg)}

.slider-wrapper{padding:40px 0;overflow:hidden;position:relative}

.slider{display:flex;position:relative}.slider__container{display:flex;padding:0 55px;transition:transform 300ms ease 100ms;z-index:3;width:100%}.slider:not(.slider--open) .item:hover{transform:scale(1.3) !important}.slider:not(.slider--open):hover .item{transform:translateX(-25%)}.slider:not(.slider--open) .item:hover ~ .item{transform:translateX(25%)}

.show-details-button{position:absolute;bottom:0;left:0;right:0;opacity:0;transition:opacity 300ms ease 100ms;border:0;outline:none;width:100%;height:100%}.show-details-button span{display:block;width:14px;margin:0 auto;color:white}

.item{flex:0 0 20.7vw;transition:transform 300ms ease 100ms;margin:0 2px;position:relative}.item img{height:100%;width:70%;vertical-align:top;border-radius:1ch}

